// import logo from "./logo.svg";
import "./App.css";
import { useState, useEffect, useRef } from "react";

function App() {
  const [src, setSrc] = useState(null);

  useEffect(() => {
    navigator.mediaDevices
      .getUserMedia({
        video: {
          facingMode: "user",
        },
      })
      .then((stream) => {
        setSrc(stream);
      });
  }, []);
  return (
    <div className="App">
      <video
        width="200px"
        height="200px"
        autoPlay
        muted
        playsInline
        ref={(video) => {
          if (!!src) {
            video.srcObject = src;
          }
        }}
        style={{ backgroundColor: "#343434" }}
      />
    </div>
  );
}

export default App;
